.filter{
    width: 340px;
    margin-right: 50px;
    background-color: #f9fafc;
    padding: 60px 60px 60px 60px;
    &__category{
        margin-bottom: 48px;
    }
    &__category-img{
        max-width: 33px;
        max-height: 34px;
        object-fit: contain;
        margin-right: 16px;
    }
    &__category-header{
        font-size: 11px;
        font-weight: bold;
        color:#949494;
        text-transform: uppercase;
        margin-bottom: 24px;
    }
    &__category-link{
        font-size: 11px;
        font-weight: bold;
        text-transform: uppercase;
        align-items: center;
        &:hover{
            text-decoration: none;
        }
    }
    &__category-item{
        margin-bottom: 16px;
    }
}
.form-filter{
    &__header{
        font-size: 11px;
        font-weight: bold;
        color:#949494;
        text-transform: uppercase;
        margin-bottom: 13px;
    }
    & .form-select{
        margin-bottom: 21px;
    }
    & .form-range{
        margin-top: 34px;
        margin-bottom: 31px;
    }
    & .form-range .noUi-handle-upper{
        //right: 0px;
    }
}
